.todo__item{
    width: 90%;
    max-width: 750px;
    min-height: 4em;
    height: auto;
    background-color: var(--black);
    margin: 0px auto 20px;
    list-style-type: none;
   border-radius: 20px;
   display: flex;
   align-items: center;
   border: 3px solid var(--white);
   overflow: hidden;
}

.todo__item__complete-button,
.todo__item__delete{
    position: relative;
    cursor: pointer;
}
.todo__item__complete-button{
    left: 20px;
    background-color: var(--black);
    width: 2em;
    height: 2em;
    border: 3px solid var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
}
.todo__item__delete{
    right: 20px;
    font-size: 1.5rem;
    opacity: .1;
}

.todo__item__text{
    /* text-align: center; */
    width: calc(100% - 9em);
    margin: 0 auto;
    padding: 5px 0;
}
.todo__item-complete{
    opacity: .5;
    border: rgb(117, 117, 117);
}
.todo__item-complete-p{
    text-decoration: line-through;
}
.icon-check{
    font-size: 1.3rem;
}
.todo__item-complete .todo__item__complete-button{
    background-color: var(--white);
    color: var(--black);
}

@media (hover: hover){
    .todo__item__delete:hover{
        color: var(--delite);
        opacity: 1;
    }
    .todo__item__complete-button:hover{
        background-color: var(--gray);
    }
    .todo__item-complete .todo__item__complete-button:hover{
        background-color: var(--gray);
        border: 2px solid var(--white);
    }
}